import React from "react";
import ReactDOM from "react-dom";
import { Catalog, pageLoader } from "catalog";

const pages = [
  {
    path: "/",
    title: "Startpage",
    styles: ['/css/bootstrap-4.1.3/bootstrap-reboot.css', '/css/bootstrap-4.1.3/bootstrap-grid.css','/css/bootstrap-custom.css','/css/style.css'],
    content: pageLoader(() => import("./WELCOME.md"))
  }, {
    path: "/typo",
    title: "Test",
    styles: ['/css/bootstrap-4.1.3/bootstrap-reboot.css', '/css/bootstrap-4.1.3/bootstrap-grid.css','/css/bootstrap-custom.css','/css/style.css'],
    // content: Catalog.pageLoader('TYPO.md')
    content: pageLoader(() => import("./TYPO.md"))
  }
];

ReactDOM.render(
  <Catalog title="Catalog" pages={pages} />,
  document.getElementById("catalog")
);


